
#app {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 10em 1fr 10em;
  grid-template-areas:
    "header"
    "main"
    "footer";
  place-items: center;
}
header {
  grid-area: header;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  border-bottom: 1px solid black;
}
header > ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
header > ul > li {
  position: relative;
}
header > ul > li:last-child {
  padding: 0 0 0 5rem;
  margin: 0 0 0 1rem;
}
header > ul > li > span {
  display: inline-block;
  padding: 1rem 1rem;
}
header > ul > li > span::after {
  position: absolute;
  content: "";
  opacity: 0.4;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 80% 76%, 61% 100%, 0 100%);
          clip-path: polygon(0 0, 100% 0, 100% 100%, 80% 76%, 61% 100%, 0 100%);
  background: gray;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
main {
  grid-area: main;
}
footer {
  grid-area: footer;
}
@media screen and (min-width: 760px) {
#app {
    grid-template-columns: 10em 1fr 10em;
    grid-template-areas:
      ".. header .."
      "..  main  .."
      ".. footer ..";
}
}

.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.buttons {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
button {
  border: none;
  margin: 1rem 0;
  width: 5rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  cursor: pointer;
  color: white;
}
.btn-success {
  background-color: rgb(5, 163, 5);
}
.btn-success:hover {
  background-color: rgb(46, 187, 46);
}
.btn-success:disabled {
  background-color: rgb(123, 206, 123);
  cursor: not-allowed;
}
.btn-fail {
  background-color: rgb(216, 100, 100);
}
.btn-fail:hover {
  background-color: rgb(221, 148, 148);
}
.btn-fail:disabled {
  background-color: rgb(216, 162, 162);
  cursor: not-allowed;
}
@-webkit-keyframes pulse {
0% {
    opacity: 1;
}
20% {
    opacity: 0.6;
}
40% {
    opacity: 0.9;
}
60% {
    opacity: 0.6;
}
100% {
    opacity: 0;
}
}
@keyframes pulse {
0% {
    opacity: 1;
}
20% {
    opacity: 0.6;
}
40% {
    opacity: 0.9;
}
60% {
    opacity: 0.6;
}
100% {
    opacity: 0;
}
}
.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.grid-modal {
  position: relative;
}
.grid-modal::after {
  position: absolute;
  content: "Hooray!";
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: grid;
  place-items: center;
  opacity: 0.8;
  font-size: 2rem;
  background-color: beige;
  -webkit-transition: opacity linear;
  transition: opacity linear;
  -webkit-animation: pulse 3s;
          animation: pulse 3s;
}

.node {
  position: relative;
  display: inline-block;
  border: 1px solid rgb(209, 197, 197);
  padding: 0.3em;
  margin: 0.05em;
  height: 0.5rem;
  width: 0.5rem;
  background-color: var(--clr);
  -webkit-transition: -webkit-transform 100ms ease-in-out;
  transition: -webkit-transform 100ms ease-in-out;
  transition: transform 100ms ease-in-out;
  transition: transform 100ms ease-in-out, -webkit-transform 100ms ease-in-out;
}
.node:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.node:hover::after {
  position: absolute;
  counter-reset: col var(--col) row var(--row);
  content: "[" counter(row) "," counter(col) "]";
  display: grid;
  place-items: center;
  top: -3rem;
  right: -3rem;
  height: 3rem;
  width: 3rem;
  background-color: rgb(194, 255, 159);
}
